<template>
	<v-container>
		<v-layout row wrap>
			<v-flex sm4 xs12
			v-for="company in companies" 
			:key="company.name"
			>
				<v-card>
					<v-card-media>
						<a :href="company.link" target="__blank" style="width: 100%">
							<img :src="company.logo" :alt="company.name">
						</a>
        			</v-card-media>
        			<v-card-title style="display: block;">
        				<h3>{{ company.name }}</h3>
        				<p>Address: {{ company.location }}</p>
        				<p>Position: {{ company.position }}</p>
        				<p>Responsibilities: {{ company.responsibility }}</p>
        				<p>Duration: {{ company.duration }}</p>
        			</v-card-title>
				</v-card>
			</v-flex>
		</v-layout>

	</v-container>
</template>

<script>
	export default {
		data() {
			return {
				companies: [
				{
					name: 'Sunhillworks Ltd',
					logo: require('../assets/sunhillworks.png'),
					link: 'https://www.facebook.com/sunhillworks/',
					location: '7th floor, Hazi Samsuddin Mansion, New Eskaton Road, Dhaka.',
					position: 'PHP/WordPress Developer',
					responsibility: 'WordPress Theme & Plugin Customization & Development as requirement',
					duration: 'October, 2016 – Continue'
				},
				{
					name: 'xpeedStudio',
					logo: require('../assets/xpeedstudio.png'),
					link: 'https://themeforest.net/user/xpeedstudio',
					location: 'Doudkandi, Comilla.',
					position: 'WordPress Support Engineer',
					responsibility: 'Client support PSD to HTML, WordPress Theme Development.',
					duration: 'April, 2016 – September, 2016'
				},
				{
					name: 'weDevs',
					logo: require('../assets/wedevs.svg'),
					link: 'https://wedevs.com/',
					location: 'Flat #F4, House #8, Road#13(new), Dhanmondi, Dhaka.',
					position: 'Support Engineer (Intern)',
					responsibility: 'Client support,Basic CSS coding',
					duration: 'November, 2015 - December, 2015'
				}
				]
			}
		}
	}
</script>

<style scoped>
	.card__media__content {
		justify-content: center !important;
	}
</style>


