<template>
  <v-app>
    <v-container>
      <v-navigation-drawer temporary absolute v-model="drawer">
        <v-list>
          <v-list-tile class="relative mb-4">
            <v-list-tile-action class="absoluteRight">
              <v-btn fab dark large color="solidBlack" @click="clearBtn()">
                <v-icon style="color: #fff">clear</v-icon>
              </v-btn>
            </v-list-tile-action>
          </v-list-tile>
<!--           <v-list-tile>
            <router-link :to="{path:'/'}" exact>
              <v-list-tile-action>
                <v-icon>home</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>Home</v-list-tile-content>
            </router-link>
          </v-list-tile> -->
          <v-list-tile 
          v-for="menu in menus" 
          :key="menu.title" 
          router 
          :to="menu.link">
            <v-list-tile-action>
              <v-icon>{{ menu.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>{{ menu.title }}</v-list-tile-content>
          </v-list-tile>
        </v-list>
        
      </v-navigation-drawer>
      <v-toolbar color="transparent" style="box-shadow: none">
        <v-toolbar-side-icon @click.stop='drawer = !drawer'></v-toolbar-side-icon>

      </v-toolbar>
    </v-container>
    <main class="">
      <router-view></router-view>
    </main>
  </v-app>
</template>

<script>
export default {
  data () {
    return {
      drawer: false,
      menus: [
        { icon: 'home', title: 'Home', link: "/home"},
        { icon: 'archive', title: 'Skills', link: "/skills"},
        { icon: 'work', title: 'Employment', link: "/employment"},
        { icon: 'build', title: 'Projects', link: "/projects"},
        { icon: 'build', title: 'Contact', link: "/contact"},


      ],
    }
  },
  methods: {
    clearBtn() {
      return this.drawer = !this.drawer
    }
  }
}
</script>

<style scoped>
.mainContent {
    width: 80%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .relative {
    position: relative;
  }
  .absoluteRight {
    position: absolute;
    right: 5%;
    display: block;
  }
</style>



